import { ReactElement } from 'react';
import { Navigate, Outlet } from 'react-router-dom';

interface PrivateRouteProps {
	children?: ReactElement; // Router.tsx PrivateRoute   Componet Element
	authentication: boolean; // true :   , false :   
}

const PrivateRoute = ({ authentication }: PrivateRouteProps): React.ReactElement | null => {
	
	/**
	  *   
	  *    : true   
	  *    : null or false(  falseN ) 
	  */
	const isAuthenticated = localStorage.getItem("isAuthenticated");

	if (authentication) {
		//  
		//      
		//    
		return (isAuthenticated === null || isAuthenticated === 'false') ? <Navigate to="/" /> : <Outlet />;

	} else {
		//  
		//      
		//    
		return (isAuthenticated === null || isAuthenticated === 'false') ? <Outlet /> : <Navigate to='/' />;
	}
}

export default PrivateRoute