import { Suspense, lazy } from 'react';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';
import PrivateRoute from './PrivateRouter';
import Layouts from '../layout/layouts';

//    Page   .
interface Page {
	path : string,
	pathName : string
} 

// Page   layz    .
interface lazyData {
	element : any
	path : string
}

// axios         pages   
const testMap = [{path :'../../pages/Main', pathName : '/main'},
						{path :'../../pages/test/TestPage1', pathName : '/testPage1'},
						{path :'../../pages/test/TestPage2', pathName : '/testPage2'}];

// lazy  
const pages : lazyData[] = [
	{element : lazy(() => import('../../pages/Main')), path : '/main'},
	{element : lazy(() => import('../../pages/test/TestPage1')), path : '/testPage1'},
	{element : lazy(() => import('../../pages/test/TestPage2')), path : '/testPage2'}
];

const Router = () => {
	const Login = lazy(() => import('../../pages/Login')); //     .
	/*         */
	//        <Route element={<PrivateRoute authentication={false}/>}>
	//          <Route path="/login" element={<LoginPage/>} />
	//        </Route>
//	<Route path="/main" element={<Layouts><Main /></Layouts>} />
	return (
		
		<BrowserRouter>
			<Suspense fallback={<div>Loading...</div>}>
				<Routes>
						{/*         "/"   login   */}
						<Route index element={<Navigate replace to="/Login"/>} />
						<Route path="/Login" element={<Login />} />
						
						{/*        */}
						<Route element={<PrivateRoute authentication={true} />}>
							{pages.map((target : lazyData) => (
					          <Route
					          		key = {target.path}
					          		path= {target.path}
					          		element={<Layouts>< target.element /></Layouts>}
					          />
					        ))}								
						</Route>
				</Routes>
			</Suspense>
		</BrowserRouter>
	)
}

export default Router